
.imagen-inicio {
  width: 100%; /* Asegurar que la imagen ocupa el 100% del ancho del contenedor */
}

/* Media query para dispositivos móviles */
@media (max-width: 600px) {
  .imagen-inicio {
    height: 200px;
    width: 100%; /* Establecer la altura deseada para dispositivos móviles */
  }
}
